body {
  margin: 0;
  font-family: 'B612', sans-serif;
  background-color: #efefef;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.mid{margin-bottom:50px}.mid .info-header-holder{width:50%;display:flex;margin:auto;justify-content:center;text-align:center;margin-top:-15px;margin-bottom:25px}.mid h1{color:#06f;font-size:275%;font-weight:900}.mid .card-container .col{margin:30px 0}.mid .card-container .col .card{border-radius:1rem !important;padding-bottom:10px;box-shadow:0 10px 50px rgba(0,0,0,0.2);border:none}.mid .card-container .col .card .card-body{padding-left:10%;padding-top:5%;height:500px}.mid .card-container .col .card .format{margin-top:0px;margin-left:-15px;margin-right:5px;background-color:#06f;width:7.5px;height:65px;float:left;border-radius:0 15px 0 15px}.mid .card-container .col .card .card-title{font-size:190%;font-weight:900;margin-right:-5%;width:220px;color:#06f;margin-bottom:10px;line-height:100%}.mid .card-container .col .card .card-img-top{padding-top:15px;padding:5% 0;margin-left:-2.3em;width:100%}.mid .card-container .col .card .globeIcon{margin-left:45px;width:65%;margin-top:35px;margin-bottom:68.3px}.mid .card-container .col .card .marketIcon{margin-left:-1em}.mid .card-container .col .card .governanceIcon{margin-left:-15px}.mid .card-container .col .card .card-text{margin-top:-50px;font-size:150%;font-weight:700;color:#919191}.mid .learnContainer{width:100%;margin:auto;text-align:center;margin-top:-10px;font-size:30px;font-weight:800;color:#06f}.mid .learnContainer .btn{margin-top:-15px;font-size:35px;font-weight:800;background-color:#06f;height:65px;width:250px;border-radius:15px;border-style:none;box-shadow:0 15px 25px rgba(0,0,0,0.2)}

.breaker{background:#06f;height:360px}

.crowdsale-container{text-align:center}.crowdsale-container .primary{color:#06f}.crowdsale-container .subtext{margin:0;color:#919191;font-weight:800}.crowdsale-container h1.primary{font-weight:800;font-size:40px}.crowdsale-container p.primary{font-weight:800;font-size:20px}.crowdsale-container p.subtext{font-size:20px}.crowdsale-container .bar img{height:680px}.crowdsale-container .sub-bar{margin:0 20%;float:center;text-align:left}.crowdsale-container .sub-bar img{height:70px;float:left}.crowdsale-container .sub-bar h1{font-size:30px;margin-bottom:0;margin-left:2.55em}.crowdsale-container .sub-bar p{font-size:25px;padding-left:3.1em}.crowdsale-container .sale-breaker{background-color:#06f}.crowdsale-container .sale-breaker h1{color:white}.crowdsale-container .sale-breaker p{color:white}

.governance-container{text-align:center}.governance-container h1{margin-top:3%;color:#06f;font-weight:800;font-size:4em}.governance-container h2{margin:8% 0;color:#919191;font-weight:800;font-size:2em;padding:0 20%}

.roadmap-container{text-align:center}.roadmap-container img{height:800px}

.topbar{padding:1% 5%}.topbar .navbar-brand{float:center}.topbar img{height:75px}.navbar{margin-top:-10px}.navbar-toggler-icon{color:rgba(0,0,0,0.5);background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")}.nav-item-container li{color:#919191;padding:0 10px;font-size:125%;font-weight:800}.nav-item-container li a:hover{color:#06f !important;cursor:pointer;transition-duration:0.3s}

.footer{width:100%;background-color:#232323;padding-bottom:35px}.footer .format{margin-top:32.5px;margin-left:-15px;background-color:#06f;width:7.5px;height:40px;float:left;border-radius:0 15px 0 15px}.footer .container{margin-left:5%;width:90%}.footer p{padding-top:30px;font-size:200%;font-weight:900;margin-bottom:0px;color:#fff}.footer ul li a{font-size:125%;padding:5px 0;width:45%;line-height:150%;color:#919191;font-weight:400}.footer ul li a:hover{color:#fff;font-weight:700;text-shadow:#000;cursor:pointer;transition-duration:0.3s}

